.delivery {
    @extend %size_screen;
    border: 1px solid gainsboro;
    font-size: 16px;
    //color: #424949;

    &_title {
        text-align: center;
    }

    &_places {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        margin-top: 50px;

        article {
            display: flex;
            margin-bottom: 20px;

            h3 {
                margin-bottom: 10px;
                font-size: 16px;
            }

            i { font-size: 30px!important; }
        }

        &-addr {
            display: flex;
            flex-direction: column;
            margin-left: 8px;

            h4 { margin-bottom: 8px; }
            span {
                font-size: 15px;
            }
        }
    }
}

@media only screen and (max-width: 850px) {
    .delivery_places {
        grid-template-columns: 1fr 1fr;
    }
}

@media only screen and (max-width: 550px) {
    .delivery_places {
        grid-template-columns: 1fr;
    }
}
